/* eslint-disable */
import { Link } from 'react-router-dom';
import { useState } from 'react'
import axios from 'axios'
import {
	Navbar,
	NavbarBrand,
	UncontrolledTooltip,
	Button,
	Modal,
	ModalHeader,
	ModalBody, 
	ModalFooter, 
	Input
} from 'reactstrap';
/* eslint-enable */

//images 
import AddT from "../img/addT.svg";
import AddTh from "../img/addTh.svg"
import AddSt from "../img/addSt.svg"
import Edit from "../img/edit.svg"
import Exit from "../img/exit.svg"
import Give from "../img/give_task.svg"

var studentName = "";
export default function Teach() {
	

	const [modal, setModal] = useState(false);
	const [nestedModal, setNestedModal] = useState(false);
	const [closeAll, setCloseAll] = useState(false);
	const toggle = () => setModal(!modal);

	const toggleNested = () => {
		setNestedModal(!nestedModal);
		setCloseAll(false);
	}

	const toggleAll = () => {
		setNestedModal(!nestedModal);
		setCloseAll(true);
	}

    return (
		<div>
			<Navbar color = "dark" dark>
				<NavbarBrand href = "/">Личный кабинет</NavbarBrand>
				<div className = "iconRow">
					<img src = {Give} alt = "Выдать задания" id = "giveic"/>
					<img src = {Edit} alt = "Редактировать базу" id = "editic"/>
					<img src = {AddSt} alt = "Добавить студента" id = "addst" onClick = {toggle}/>
					<img src = {AddT} alt = "Добавить задание" id = "addt"/>
					<img src = {AddTh} alt = "Добавить теорию" id = "addth"/>
					<img src = {Exit} alt = "Выйти" id = "exitic"/>

					<UncontrolledTooltip placement= "bottom" target = "addt">
						Добавить задание
					</UncontrolledTooltip>
					<UncontrolledTooltip placement= "bottom" target = "addth">
						Добавить теорию
					</UncontrolledTooltip>
					<UncontrolledTooltip placement= "bottom" target = "addst">
						Добавить студента
					</UncontrolledTooltip>
					<UncontrolledTooltip placement= "bottom" target = "editic">
						Редактировать базу
					</UncontrolledTooltip>
					<UncontrolledTooltip placement= "bottom" target = "giveic">
						Выдать задания
					</UncontrolledTooltip>
					<UncontrolledTooltip placement= "bottom" target = "exitic">
						Выйти
					</UncontrolledTooltip>
				</div>
			</Navbar>
			<div className = "teacherList">
				<h2>Список студентов:</h2>
			</div>
			<Modal isOpen={modal} toggle={toggle}>
				<ModalHeader toggle={toggle}>Добавьте студента</ModalHeader>
					<ModalBody>
						<Input type = "textarea" placeholder = "Введите ФИО студента" 
						onChange = {(e) => {
							studentName = e.target.value;
						}
						}></Input>
					<br />
					<Modal isOpen={nestedModal} toggle={toggleNested} onClosed={closeAll ? toggle : undefined}>
						<ModalHeader>Ссылка-приглашение</ModalHeader>
						<ModalBody>
							Отправьте следующую ссылку вашему ученику - {() => {
								// this shit DOESN'T WORK
								var str = window.location.origin + "/register?id=" + Math.floor(Math.random() * 100);
								console.log(str);
								return str;
							}}
						</ModalBody>
						<ModalFooter>
							<Button onClick={toggleNested}>Добавить еще одного ученика</Button>
							<Button onClick={toggleAll}>Выйти</Button>
						</ModalFooter>
					</Modal>
					</ModalBody>
					<ModalFooter>
					<Button color="primary" onClick={toggleNested}>Ок</Button>{' '}
					<Button color="secondary" onClick={toggle}>Отмена</Button>
				</ModalFooter>
			</Modal>
		</div>
		
    )
}

function newStGetID() {
	console.log(studentName);
	
}